#hero {
  padding-bottom: 100px;
  background-color: #F8FFFB;
}
#hero .person-wrap {
  display: flex;
  justify-content: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 35px;
}
#hero .person-wrap .person-description {
  max-width: 360px;
}

#solve-tasks .consultation img {
  min-width: 265px;
}
#solve-tasks .consultation .default-button {
  width: 100%;
}

#recommend {
  padding-bottom: 40px;
}
#recommend #recommend-slider-wrap {
  margin-top: 30px;
}

section#last-works .statistics {
  background-image: url(../../assets/img/svg/waves.svg);
  background-position: center bottom;
  background-size: contain;
  background-repeat: no-repeat;
}

section#work-with-us {
  background-color: #F8FFFB;
  background-image: url("../../assets/img/svg/work-with-us-bg.svg");
  background-repeat: no-repeat;
  background-position: right bottom;
}

.work-with-us-card picture:after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #FFFFFF;
  z-index: 0;
  margin: 2px;
  border-radius: 50%;
}
.work-with-us-card picture img {
  position: relative;
  z-index: 1;
}

section#about-us-talk .description {
  position: relative;
}
section#about-us-talk .description:after {
  content: "";
  position: absolute;
  left: calc(8% - (30px / 2));
  bottom: calc(0% - (30px / 2));
  width: 30px;
  height: 30px;
  transform: rotate(45deg);
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  background-color: #fff;
}

section#brands {
  background-image: url("../../assets/img/svg/brands-bg.svg");
  background-position: left bottom;
  background-repeat: no-repeat;
}
section#brands .section-title {
  text-align: left;
  font-size: 46px;
}
section#brands .default-button {
  min-width: 295px;
}
section#brands .description p {
  margin-bottom: 40px;
}
section#brands .description p:last-child {
  margin-bottom: 0;
}

section#about-us .description p {
  margin-bottom: 40px;
}
section#about-us .description p:last-child {
  margin-bottom: 0;
}
section#about-us .default-button {
  min-width: 295px;
}

#recommend-slider-wrap {
  margin: 0 -75px;
}
#recommend-slider-wrap #recommend-slider {
  margin: 0 75px;
}
#recommend-slider-wrap .slide-wrap {
  padding: calc(30px / 2);
  transition: all 0.2s ease-in-out;
}
#recommend-slider-wrap .slide-wrap a {
  cursor: pointer;
}
#recommend-slider-wrap .slide-wrap picture {
  display: block;
  border: 1px solid #C5C5C5;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  overflow: hidden;
}
#recommend-slider-wrap .swiper-slide:hover .slide-wrap {
  transform: scale(1.1);
}

#last-works-slider-wrap .links-group {
  display: flex;
  padding: 0 65px;
  padding: 0 65px;
}
#last-works-slider-wrap .links-group a {
  flex: 1;
}
#last-works-slider-wrap .links-group a:first-child {
  margin-right: 10px;
}
#last-works-slider-wrap .slider-nav-button {
  top: calc(100% - 55px);
}

.last-work-card b {
  color: #343A3F;
}